namespace $ { export class $mol_switcher extends $mol_viewer {

	/// heightMinimal 44
	heightMinimal() {
		return 44
	}

	/// enabled true
	enabled() {
		return true
	}

	/// optionChecked# false
	@ $mol_mem_key()
	optionChecked( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// optionLabel# \
	optionLabel( key : any ) {
		return ""
	}

	/// optioner# $mol_checker 
	/// 	checked > optionChecked# 
	/// 	label / < optionLabel# 
	/// 	enabled < enabled
	@ $mol_mem_key()
	optioner( key : any , next? : any ) {
		return new $mol_checker().setup( obj => { 
			obj.checked = ( next? : any ) => this.optionChecked( key , next )
			obj.label = () => [].concat( this.optionLabel( key ) )
			obj.enabled = () => this.enabled()
		} )
	}

	/// value null
	@ $mol_mem()
	value( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// options *
	options() {
		return ({
		})
	}

	/// items /
	items() {
		return [] as any[]
	}

	/// childs < items
	childs() {
		return this.items()
	}

} }

