namespace $ { export class $mol_checker_expander_demo extends $mol_rower {

	/// c1Label @ \Collapsed
	c1Label() {
		return this.localizedText( "c1Label" )
	}

	/// c1 $mol_checker_expander label / < c1Label
	@ $mol_mem()
	c1( next? : any ) {
		return new $mol_checker_expander().setup( obj => { 
			obj.label = () => [].concat( this.c1Label() )
		} )
	}

	/// c2Label @ \Expanded
	c2Label() {
		return this.localizedText( "c2Label" )
	}

	/// c2 $mol_checker_expander 
	/// 	label / < c2Label 
	/// 	checked true
	@ $mol_mem()
	c2( next? : any ) {
		return new $mol_checker_expander().setup( obj => { 
			obj.label = () => [].concat( this.c2Label() )
			obj.checked = () => true
		} )
	}

	/// c3 $mol_checker_expander
	@ $mol_mem()
	c3( next? : any ) {
		return new $mol_checker_expander()
	}

	/// c4 $mol_checker_expander checked true
	@ $mol_mem()
	c4( next? : any ) {
		return new $mol_checker_expander().setup( obj => { 
			obj.checked = () => true
		} )
	}

	/// c5Label @ \Non expandable
	c5Label() {
		return this.localizedText( "c5Label" )
	}

	/// c5 $mol_checker_expander 
	/// 	label / < c5Label 
	/// 	checked true 
	/// 	disabled true
	@ $mol_mem()
	c5( next? : any ) {
		return new $mol_checker_expander().setup( obj => { 
			obj.label = () => [].concat( this.c5Label() )
			obj.checked = () => true
			obj.disabled = () => true
		} )
	}

	/// childs / 
	/// 	< c1 
	/// 	< c2 
	/// 	< c3 
	/// 	< c4 
	/// 	< c5
	childs() {
		return [].concat( this.c1() , this.c2() , this.c3() , this.c4() , this.c5() )
	}

} }

