namespace $ { export class $mol_expander extends $mol_lister {

	/// expanded false
	@ $mol_mem()
	expanded( next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// label /
	label() {
		return [] as any[]
	}

	/// labeler $mol_checker_expander 
	/// 	checked > expanded 
	/// 	label < label
	@ $mol_mem()
	labeler( next? : any ) {
		return new $mol_checker_expander().setup( obj => { 
			obj.checked = ( next? : any ) => this.expanded( next )
			obj.label = () => this.label()
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// rows / 
	/// 	< labeler 
	/// 	< content
	rows() {
		return [].concat( this.labeler() , this.content() )
	}

} }

