	@ $mol_mem()
	signup( next? : any ) {
		return new $mol_app_signup()
	}

	/// body / < signup
	body() {
		return [].concat( this.signup() )
	}

	/// rower $mol_rower_demo
	@ $mol_mem()
	rower( next? : any ) {
		return new $mol_rower_demo()
	}

