namespace $ { export class $mol_app_report extends $mol_pager {

	/// title \Pump #1337
	title() {
		return "Pump #1337"
	}

	/// description \
	description() {
		return ""
	}

	/// descriptor $mol_viewer childs / < description
	@ $mol_mem()
	descriptor( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.description() )
		} )
	}

	/// headCells /
	headCells() {
		return [] as any[]
	}

	/// headRower $mol_app_report_rower cells < headCells
	@ $mol_mem()
	headRower( next? : any ) {
		return new $mol_app_report_rower().setup( obj => { 
			obj.cells = () => this.headCells()
		} )
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// tabler $mol_app_report_tabler rows / 
	/// 	< headRower 
	/// 	< rows
	@ $mol_mem()
	tabler( next? : any ) {
		return new $mol_app_report_tabler().setup( obj => { 
			obj.rows = () => [].concat( this.headRower() , this.rows() )
		} )
	}

	/// body / 
	/// 	< descriptor 
	/// 	< tabler
	body() {
		return [].concat( this.descriptor() , this.tabler() )
	}

	/// rowerCells# /
	rowerCells( key : any ) {
		return [] as any[]
	}

	/// rower# $mol_app_report_rower cells < rowerCells#
	@ $mol_mem_key()
	rower( key : any , next? : any ) {
		return new $mol_app_report_rower().setup( obj => { 
			obj.cells = () => this.rowerCells( key )
		} )
	}

	/// cellContent# null
	cellContent( key : any ) {
		return <any> null
	}

	/// cellRows# 1
	cellRows( key : any ) {
		return 1
	}

	/// cellCols# 1
	cellCols( key : any ) {
		return 1
	}

	/// celler# $mol_app_report_celler 
	/// 	content < cellContent# 
	/// 	rows < cellRows# 
	/// 	cols < cellCols#
	@ $mol_mem_key()
	celler( key : any , next? : any ) {
		return new $mol_app_report_celler().setup( obj => { 
			obj.content = () => this.cellContent( key )
			obj.rows = () => this.cellRows( key )
			obj.cols = () => this.cellCols( key )
		} )
	}

	/// cellValue# null
	@ $mol_mem_key()
	cellValue( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// texter# $mol_viewer childs / < cellValue#
	@ $mol_mem_key()
	texter( key : any , next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.cellValue( key ) )
		} )
	}

	/// stringer# $mol_stringer value > cellValue#
	@ $mol_mem_key()
	stringer( key : any , next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.cellValue( key , next )
		} )
	}

	/// number# $mol_number value > cellValue#
	@ $mol_mem_key()
	number( key : any , next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.cellValue( key , next )
		} )
	}

} }

namespace $ { export class $mol_app_report_tabler extends $mol_viewer {

	/// tagName \table
	tagName() {
		return "table"
	}

	/// rows /
	rows() {
		return [] as any[]
	}

	/// childs < rows
	childs() {
		return this.rows()
	}

} }

namespace $ { export class $mol_app_report_rower extends $mol_viewer {

	/// tagName \tr
	tagName() {
		return "tr"
	}

	/// cells /
	cells() {
		return [] as any[]
	}

	/// childs < cells
	childs() {
		return this.cells()
	}

} }

namespace $ { export class $mol_app_report_celler extends $mol_viewer {

	/// tagName \td
	tagName() {
		return "td"
	}

	/// cols 1
	cols() {
		return 1
	}

	/// rows 1
	rows() {
		return 1
	}

	/// attr * 
	/// 	colspan < cols 
	/// 	rowspan < rows
	attr() {
		return $mol_merge_dict( super.attr() , {
			"colspan" : ()=> <any> this.cols() ,
			"rowspan" : ()=> <any> this.rows() ,
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// childs / < content
	childs() {
		return [].concat( this.content() )
	}

} }

