namespace $ { export class $mol_sectioner extends $mol_lister {

	/// head null
	head() {
		return <any> null
	}

	/// header $mol_viewer childs / < head
	@ $mol_mem()
	header( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.head() )
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// rows / 
	/// 	< header 
	/// 	< content
	rows() {
		return [].concat( this.header() , this.content() )
	}

} }

