namespace $ { export class $mol_checker_ticker_demo extends $mol_rower {

	/// c1Label @ \Unchecked
	c1Label() {
		return this.localizedText( "c1Label" )
	}

	/// c1 $mol_checker_ticker label / < c1Label
	@ $mol_mem()
	c1( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.label = () => [].concat( this.c1Label() )
		} )
	}

	/// c2Label @ \Checked
	c2Label() {
		return this.localizedText( "c2Label" )
	}

	/// c2 $mol_checker_ticker 
	/// 	label / < c2Label 
	/// 	checked true
	@ $mol_mem()
	c2( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.label = () => [].concat( this.c2Label() )
			obj.checked = () => true
		} )
	}

	/// c3 $mol_checker_ticker
	@ $mol_mem()
	c3( next? : any ) {
		return new $mol_checker_ticker()
	}

	/// c4 $mol_checker_ticker checked true
	@ $mol_mem()
	c4( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.checked = () => true
		} )
	}

	/// c5 $mol_checker_ticker disabled true
	@ $mol_mem()
	c5( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.disabled = () => true
		} )
	}

	/// c6Label @ \Disabled
	c6Label() {
		return this.localizedText( "c6Label" )
	}

	/// c6 $mol_checker_ticker 
	/// 	label / < c6Label 
	/// 	checked true 
	/// 	disabled true
	@ $mol_mem()
	c6( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.label = () => [].concat( this.c6Label() )
			obj.checked = () => true
			obj.disabled = () => true
		} )
	}

	/// childs / 
	/// 	< c1 
	/// 	< c2 
	/// 	< c3 
	/// 	< c4 
	/// 	< c5 
	/// 	< c6
	childs() {
		return [].concat( this.c1() , this.c2() , this.c3() , this.c4() , this.c5() , this.c6() )
	}

} }

