namespace $ { export class $mol_decker_demo extends $mol_decker {

	/// stringerLabel @ \String fields
	stringerLabel() {
		return this.localizedText( "stringerLabel" )
	}

	/// stringerContent $mol_stringer_demo
	@ $mol_mem()
	stringerContent( next? : any ) {
		return new $mol_stringer_demo()
	}

	/// stringerItem $mol_decker_item 
	/// 	title < stringerLabel 
	/// 	content < stringerContent
	@ $mol_mem()
	stringerItem( next? : any ) {
		return new $mol_decker_item().setup( obj => { 
			obj.title = () => this.stringerLabel()
			obj.content = () => this.stringerContent()
		} )
	}

	/// buttonsLabel @ \Buttons
	buttonsLabel() {
		return this.localizedText( "buttonsLabel" )
	}

	/// clickerContent $mol_clicker_demo
	@ $mol_mem()
	clickerContent( next? : any ) {
		return new $mol_clicker_demo()
	}

	/// clickerItem $mol_decker_item 
	/// 	title < buttonsLabel 
	/// 	content < clickerContent
	@ $mol_mem()
	clickerItem( next? : any ) {
		return new $mol_decker_item().setup( obj => { 
			obj.title = () => this.buttonsLabel()
			obj.content = () => this.clickerContent()
		} )
	}

	/// checkerLabel @ \Check boxes
	checkerLabel() {
		return this.localizedText( "checkerLabel" )
	}

	/// checkerContent $mol_checker_ticker_demo
	@ $mol_mem()
	checkerContent( next? : any ) {
		return new $mol_checker_ticker_demo()
	}

	/// checkerItem $mol_decker_item 
	/// 	title < checkerLabel 
	/// 	content < checkerContent
	@ $mol_mem()
	checkerItem( next? : any ) {
		return new $mol_decker_item().setup( obj => { 
			obj.title = () => this.checkerLabel()
			obj.content = () => this.checkerContent()
		} )
	}

	/// items / 
	/// 	< stringerItem 
	/// 	< clickerItem 
	/// 	< checkerItem
	items() {
		return [].concat( this.stringerItem() , this.clickerItem() , this.checkerItem() )
	}

} }

