namespace $ { export class $mol_dimmer_demo extends $mol_rower {

	/// one $mol_dimmer 
	/// 	haystack \Don't put all your eggs in one basket
	/// 	needle \eggs
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "Don't put all your eggs in one basket"
			obj.needle = () => "eggs"
		} )
	}

	/// two $mol_dimmer 
	/// 	haystack \Don't look a gift horse in the mouth.
	/// 	needle \oo
	@ $mol_mem()
	two( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "Don't look a gift horse in the mouth."
			obj.needle = () => "oo"
		} )
	}

	/// three $mol_dimmer 
	/// 	haystack \There is no word you are looking for
	/// 	needle \luck
	@ $mol_mem()
	three( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "There is no word you are looking for"
			obj.needle = () => "luck"
		} )
	}

	/// four $mol_dimmer 
	/// 	haystack \ooAAooAAoo
	/// 	needle \oo
	@ $mol_mem()
	four( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "ooAAooAAoo"
			obj.needle = () => "oo"
		} )
	}

	/// five $mol_dimmer 
	/// 	haystack \Let's search this string
	/// 	needle \Let's search this string
	@ $mol_mem()
	five( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "Let's search this string"
			obj.needle = () => "Let's search this string"
		} )
	}

	/// six $mol_dimmer 
	/// 	haystack \Let's search nothing
	/// 	needle \
	@ $mol_mem()
	six( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => "Let's search nothing"
			obj.needle = () => ""
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three 
	/// 	< four 
	/// 	< five 
	/// 	< six
	childs() {
		return [].concat( this.one() , this.two() , this.three() , this.four() , this.five() , this.six() )
	}

} }

