namespace $ { export class $mol_form_demo extends $mol_form {

	/// loginLabel @ \Login
	loginLabel() {
		return this.localizedText( "loginLabel" )
	}

	/// loginErrors /
	loginErrors() {
		return [] as any[]
	}

	/// login \
	@ $mol_mem()
	login( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// loginControl $mol_stringer value > login
	@ $mol_mem()
	loginControl( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.login( next )
		} )
	}

	/// loginField $mol_form_field 
	/// 	name < loginLabel 
	/// 	errors < loginErrors 
	/// 	control < loginControl
	@ $mol_mem()
	loginField( next? : any ) {
		return new $mol_form_field().setup( obj => { 
			obj.name = () => this.loginLabel()
			obj.errors = () => this.loginErrors()
			obj.control = () => this.loginControl()
		} )
	}

	/// passwordLabel @ \Password
	passwordLabel() {
		return this.localizedText( "passwordLabel" )
	}

	/// passwordErrors /
	passwordErrors() {
		return [] as any[]
	}

	/// password \
	@ $mol_mem()
	password( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// passControl $mol_stringer 
	/// 	value > password 
	/// 	type \password
	@ $mol_mem()
	passControl( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.password( next )
			obj.type = () => "password"
		} )
	}

	/// passwordField $mol_form_field 
	/// 	name < passwordLabel 
	/// 	errors < passwordErrors 
	/// 	control < passControl
	@ $mol_mem()
	passwordField( next? : any ) {
		return new $mol_form_field().setup( obj => { 
			obj.name = () => this.passwordLabel()
			obj.errors = () => this.passwordErrors()
			obj.control = () => this.passControl()
		} )
	}

	/// formFields / 
	/// 	< loginField 
	/// 	< passwordField
	formFields() {
		return [].concat( this.loginField() , this.passwordField() )
	}

	/// submitText @ \Sign In
	submitText() {
		return this.localizedText( "submitText" )
	}

	/// eventSubmit null
	@ $mol_mem()
	eventSubmit( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// submit $mol_clicker_major 
	/// 	childs / < submitText 
	/// 	eventClick > eventSubmit 
	/// 	disabled < submitBlocked
	@ $mol_mem()
	submit( next? : any ) {
		return new $mol_clicker_major().setup( obj => { 
			obj.childs = () => [].concat( this.submitText() )
			obj.eventClick = ( next? : any ) => this.eventSubmit( next )
			obj.disabled = () => this.submitBlocked()
		} )
	}

	/// buttons / < submit
	buttons() {
		return [].concat( this.submit() )
	}

} }

