namespace $ { export class $mol_number_demo extends $mol_rower {

	/// zero $mol_number
	@ $mol_mem()
	zero( next? : any ) {
		return new $mol_number()
	}

	/// year null
	@ $mol_mem()
	year( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// one $mol_number value > year
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
		} )
	}

	/// two $mol_number 
	/// 	value > year 
	/// 	hint \2016
	@ $mol_mem()
	two( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.hint = () => "2016"
		} )
	}

	/// age 32
	@ $mol_mem()
	age( next? : any ) {
		return ( next !== void 0 ) ? next : 32
	}

	/// three $mol_number 
	/// 	value > age 
	/// 	hint \18-99
	/// 	enabled false
	@ $mol_mem()
	three( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.age( next )
			obj.hint = () => "18-99"
			obj.enabled = () => false
		} )
	}

	/// four $mol_number 
	/// 	value > year 
	/// 	enabledStringer false
	@ $mol_mem()
	four( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.enabledStringer = () => false
		} )
	}

	/// five $mol_number 
	/// 	value > age 
	/// 	enabledDec false
	@ $mol_mem()
	five( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.age( next )
			obj.enabledDec = () => false
		} )
	}

	/// six $mol_number 
	/// 	value > year 
	/// 	enabledInc false
	@ $mol_mem()
	six( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.enabledInc = () => false
		} )
	}

	/// seven $mol_number 
	/// 	value > year 
	/// 	precisionChange 10
	@ $mol_mem()
	seven( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.precisionChange = () => 10
		} )
	}

	/// eight $mol_number 
	/// 	value > year 
	/// 	precisionView 0.01
	@ $mol_mem()
	eight( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.precisionView = () => 0.01
		} )
	}

	/// nine $mol_number 
	/// 	value > year 
	/// 	precision 1000
	@ $mol_mem()
	nine( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.year( next )
			obj.precision = () => 1000
		} )
	}

	/// childs / 
	/// 	< zero 
	/// 	< one 
	/// 	< two 
	/// 	< three 
	/// 	< four 
	/// 	< five 
	/// 	< six 
	/// 	< seven 
	/// 	< eight 
	/// 	< nine
	childs() {
		return [].concat( this.zero() , this.one() , this.two() , this.three() , this.four() , this.five() , this.six() , this.seven() , this.eight() , this.nine() )
	}

} }

