namespace $ { export class $mol_coder extends $mol_viewer {

	/// value \
	@ $mol_mem()
	value( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// format \
	format() {
		return ""
	}

	/// hint < format
	hint() {
		return this.format()
	}

	/// manualer $mol_stringer 
	/// 	value > value 
	/// 	hint < hint
	@ $mol_mem()
	manualer( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.value( next )
			obj.hint = () => this.hint()
		} )
	}

	/// eventScan null
	@ $mol_mem()
	eventScan( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// labelScan @ \Scan
	labelScan() {
		return this.localizedText( "labelScan" )
	}

	/// scanner $mol_clicker 
	/// 	eventClick > eventScan 
	/// 	childs / < labelScan
	@ $mol_mem()
	scanner( next? : any ) {
		return new $mol_clicker().setup( obj => { 
			obj.eventClick = ( next? : any ) => this.eventScan( next )
			obj.childs = () => [].concat( this.labelScan() )
		} )
	}

	/// childs / 
	/// 	< manualer 
	/// 	< scanner
	childs() {
		return [].concat( this.manualer() , this.scanner() )
	}

} }

