namespace $ { export class $mol_coster extends $mol_viewer {

	/// value null
	value() {
		return <any> null
	}

	/// prefix \
	prefix() {
		return ""
	}

	/// prefixer $mol_viewer childs / < prefix
	@ $mol_mem()
	prefixer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.prefix() )
		} )
	}

	/// valueView \
	valueView() {
		return ""
	}

	/// mainer $mol_viewer childs / < valueView
	@ $mol_mem()
	mainer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.valueView() )
		} )
	}

	/// postfix \
	postfix() {
		return ""
	}

	/// postfixer $mol_viewer childs / < postfix
	@ $mol_mem()
	postfixer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.postfix() )
		} )
	}

	/// childs / 
	/// 	< prefixer 
	/// 	< mainer 
	/// 	< postfixer
	childs() {
		return [].concat( this.prefixer() , this.mainer() , this.postfixer() )
	}

} }

