namespace $ { export class $mol_form extends $mol_viewer {

	/// submitBlocked false
	submitBlocked() {
		return false
	}

	/// formFields /
	formFields() {
		return [] as any[]
	}

	/// barFields $mol_viewer childs < formFields
	@ $mol_mem()
	barFields( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => this.formFields()
		} )
	}

	/// buttons /
	buttons() {
		return [] as any[]
	}

	/// barButtons $mol_rower childs < buttons
	@ $mol_mem()
	barButtons( next? : any ) {
		return new $mol_rower().setup( obj => { 
			obj.childs = () => this.buttons()
		} )
	}

	/// childs / 
	/// 	< barFields 
	/// 	< barButtons
	childs() {
		return [].concat( this.barFields() , this.barButtons() )
	}

} }

