namespace $ { export class $mol_portioner_demo extends $mol_rower {

	/// one $mol_portioner portion 0
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_portioner().setup( obj => { 
			obj.portion = () => 0
		} )
	}

	/// two $mol_portioner portion 0.5
	@ $mol_mem()
	two( next? : any ) {
		return new $mol_portioner().setup( obj => { 
			obj.portion = () => 0.5
		} )
	}

	/// three $mol_portioner portion 1
	@ $mol_mem()
	three( next? : any ) {
		return new $mol_portioner().setup( obj => { 
			obj.portion = () => 1
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three
	childs() {
		return [].concat( this.one() , this.two() , this.three() )
	}

} }

