namespace $ { export class $mol_labeler_demo_text extends $mol_labeler {

	/// title @ \Provider
	title() {
		return this.localizedText( "title" )
	}

	/// content @ \ACME Provider Inc.
	content() {
		return this.localizedText( "content" )
	}

} }

namespace $ { export class $mol_labeler_demo_stringer extends $mol_labeler {

	/// title @ \User name
	title() {
		return this.localizedText( "title" )
	}

	/// hint @ \Jack Sparrow
	hint() {
		return this.localizedText( "hint" )
	}

	/// userName \
	@ $mol_mem()
	userName( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// content $mol_stringer 
	/// 	hint < hint 
	/// 	value > userName
	@ $mol_mem()
	content( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.hint = () => this.hint()
			obj.value = ( next? : any ) => this.userName( next )
		} )
	}

} }

