namespace $ { export class $mol_app_inventory_controller extends $mol_pager {

	/// domain $mol_app_inventory_domain
	@ $mol_mem()
	domain( next? : any ) {
		return new $mol_app_inventory_domain()
	}

	/// positioners /
	positioners() {
		return [] as any[]
	}

	/// body < positioners
	body() {
		return this.positioners()
	}

	/// position# null
	position( key : any ) {
		return <any> null
	}

	/// positioner# $mol_app_inventory_positioner position < position#
	@ $mol_mem_key()
	positioner( key : any , next? : any ) {
		return new $mol_app_inventory_positioner().setup( obj => { 
			obj.position = () => this.position( key )
		} )
	}

	/// eventSubmit null
	@ $mol_mem()
	eventSubmit( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// submitLabel @ \Remove confirmed
	submitLabel() {
		return this.localizedText( "submitLabel" )
	}

	/// submitter $mol_clicker_major 
	/// 	eventClick > eventSubmit 
	/// 	childs / < submitLabel
	@ $mol_mem()
	submitter( next? : any ) {
		return new $mol_clicker_major().setup( obj => { 
			obj.eventClick = ( next? : any ) => this.eventSubmit( next )
			obj.childs = () => [].concat( this.submitLabel() )
		} )
	}

	/// controlsRower $mol_rower childs / < submitter
	@ $mol_mem()
	controlsRower( next? : any ) {
		return new $mol_rower().setup( obj => { 
			obj.childs = () => [].concat( this.submitter() )
		} )
	}

	/// foot / < controlsRower
	foot() {
		return [].concat( this.controlsRower() )
	}

} }

