namespace $ { export class $mol_barer_demo_search extends $mol_barer {

	/// value \
	@ $mol_mem()
	value( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// stringer $mol_stringer value > value
	@ $mol_mem()
	stringer( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.value( next )
		} )
	}

	/// submitter $mol_clicker childs / \Submit
	@ $mol_mem()
	submitter( next? : any ) {
		return new $mol_clicker().setup( obj => { 
			obj.childs = () => [].concat( "Submit" )
		} )
	}

	/// childs / 
	/// 	< stringer 
	/// 	< submitter
	childs() {
		return [].concat( this.stringer() , this.submitter() )
	}

} }

