namespace $ { export class $mol_scroller_demo extends $mol_scroller {

	/// one $mol_filler
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_filler()
	}

	/// childs / < one
	childs() {
		return [].concat( this.one() )
	}

} }

