namespace $ { export class $mol_rower_demo extends $mol_rower {

	/// heightMinimal 68
	heightMinimal() {
		return 68
	}

	/// helloHint @ \Title
	helloHint() {
		return this.localizedText( "helloHint" )
	}

	/// title \
	@ $mol_mem()
	title( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// suggest1 @ \Hello
	suggest1() {
		return this.localizedText( "suggest1" )
	}

	/// suggest2 @ \World
	suggest2() {
		return this.localizedText( "suggest2" )
	}

	/// titler $mol_suggester 
	/// 	hint < helloHint 
	/// 	value > title 
	/// 	suggests / 
	/// 		< suggest1 
	/// 		< suggest2
	@ $mol_mem()
	titler( next? : any ) {
		return new $mol_suggester().setup( obj => { 
			obj.hint = () => this.helloHint()
			obj.value = ( next? : any ) => this.title( next )
			obj.suggests = () => [].concat( this.suggest1() , this.suggest2() )
		} )
	}

	/// countHint @ \Count
	countHint() {
		return this.localizedText( "countHint" )
	}

	/// count null
	@ $mol_mem()
	count( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// counter $mol_number 
	/// 	hint < countHint 
	/// 	value > count
	@ $mol_mem()
	counter( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.hint = () => this.countHint()
			obj.value = ( next? : any ) => this.count( next )
		} )
	}

	/// progress 0.33
	progress() {
		return 0.33
	}

	/// progresser $mol_portioner portion < progress
	@ $mol_mem()
	progresser( next? : any ) {
		return new $mol_portioner().setup( obj => { 
			obj.portion = () => this.progress()
		} )
	}

	/// publishLabel @ \Shared
	publishLabel() {
		return this.localizedText( "publishLabel" )
	}

	/// publish false
	@ $mol_mem()
	publish( next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// publisher $mol_checker_ticker 
	/// 	label / < publishLabel 
	/// 	checked > publish
	@ $mol_mem()
	publisher( next? : any ) {
		return new $mol_checker_ticker().setup( obj => { 
			obj.label = () => [].concat( this.publishLabel() )
			obj.checked = ( next? : any ) => this.publish( next )
		} )
	}

	/// dropLabel @ \Drop
	dropLabel() {
		return this.localizedText( "dropLabel" )
	}

	/// eventLog null
	@ $mol_mem()
	eventLog( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// buttonDrop $mol_clicker_minor 
	/// 	childs / < dropLabel 
	/// 	eventClick > eventLog
	@ $mol_mem()
	buttonDrop( next? : any ) {
		return new $mol_clicker_minor().setup( obj => { 
			obj.childs = () => [].concat( this.dropLabel() )
			obj.eventClick = ( next? : any ) => this.eventLog( next )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< counter 
	/// 	< progresser 
	/// 	< publisher 
	/// 	< buttonDrop
	childs() {
		return [].concat( this.titler() , this.counter() , this.progresser() , this.publisher() , this.buttonDrop() )
	}

} }

