namespace $ { export class $mol_carder extends $mol_lister {

	/// status \
	status() {
		return ""
	}

	/// attr * mol_carder_status < status
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_carder_status" : ()=> <any> this.status() ,
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// contenter $mol_viewer childs / < content
	@ $mol_mem()
	contenter( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.content() )
		} )
	}

	/// statusText < status
	statusText() {
		return this.status()
	}

	/// statuser $mol_viewer childs / < statusText
	@ $mol_mem()
	statuser( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.statusText() )
		} )
	}

	/// rows / 
	/// 	< contenter 
	/// 	< statuser
	rows() {
		return [].concat( this.contenter() , this.statuser() )
	}

} }

