namespace $ { export class $mol_form_field extends $mol_viewer {

	/// name \
	name() {
		return ""
	}

	/// namer $mol_viewer childs / < name
	@ $mol_mem()
	namer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.name() )
		} )
	}

	/// errors /
	errors() {
		return [] as any[]
	}

	/// errorer $mol_viewer childs < errors
	@ $mol_mem()
	errorer( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => this.errors()
		} )
	}

	/// label $mol_viewer childs / 
	/// 	< namer 
	/// 	< errorer
	@ $mol_mem()
	label( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.namer() , this.errorer() )
		} )
	}

	/// control null
	control() {
		return <any> null
	}

	/// childs / 
	/// 	< label 
	/// 	< control
	childs() {
		return [].concat( this.label() , this.control() )
	}

} }

