namespace $ { export class $mol_app_quine extends $mol_pager {

	/// content \
	content() {
		return ""
	}

	/// texter $mol_texter text < content
	@ $mol_mem()
	texter( next? : any ) {
		return new $mol_texter().setup( obj => { 
			obj.text = () => this.content()
		} )
	}

	/// body / < texter
	body() {
		return [].concat( this.texter() )
	}

} }

