namespace $ { export class $mol_demo extends $mol_viewer {

	/// name \$mol_viewer
	name() {
		return "$mol_viewer "
	}

	/// title < name
	title() {
		return this.name()
	}

	/// titler $mol_viewer childs / < title
	@ $mol_mem()
	titler( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.title() )
		} )
	}

	/// widget null
	widget() {
		return <any> null
	}

	/// screener $mol_viewer childs / < widget
	@ $mol_mem()
	screener( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.widget() )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< screener
	childs() {
		return [].concat( this.titler() , this.screener() )
	}

} }

