namespace $ { export class $mol_dimmer extends $mol_viewer {

	/// haystack \
	@ $mol_mem()
	haystack( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// needle \
	@ $mol_mem()
	needle( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// parts /
	parts() {
		return [] as any[]
	}

	/// childs < parts
	childs() {
		return this.parts()
	}

	/// string# \
	string( key : any ) {
		return ""
	}

	/// low# $mol_dimmer_low childs / < string#
	@ $mol_mem_key()
	low( key : any , next? : any ) {
		return new $mol_dimmer_low().setup( obj => { 
			obj.childs = () => [].concat( this.string( key ) )
		} )
	}

} }

namespace $ { export class $mol_dimmer_low extends $mol_viewer {

	/// tagName \span
	tagName() {
		return "span"
	}

} }

