namespace $ { export class $mol_demo_all extends $mol_viewer {

	/// name \$mol_viewer
	name() {
		return "$mol_viewer"
	}

	/// mediumLabel \Fit to content
	mediumLabel() {
		return "Fit to content"
	}

	/// medium $mol_demo_medium 
	/// 	name < name 
	/// 	title < mediumLabel
	@ $mol_mem()
	medium( next? : any ) {
		return new $mol_demo_medium().setup( obj => { 
			obj.name = () => this.name()
			obj.title = () => this.mediumLabel()
		} )
	}

	/// smallLabel @ \Minimum screen
	smallLabel() {
		return this.localizedText( "smallLabel" )
	}

	/// small $mol_demo_small 
	/// 	name < name 
	/// 	title < smallLabel
	@ $mol_mem()
	small( next? : any ) {
		return new $mol_demo_small().setup( obj => { 
			obj.name = () => this.name()
			obj.title = () => this.smallLabel()
		} )
	}

	/// largeLabel @ \Maximize to screen
	largeLabel() {
		return this.localizedText( "largeLabel" )
	}

	/// large $mol_demo_large 
	/// 	name < name 
	/// 	title < largeLabel
	@ $mol_mem()
	large( next? : any ) {
		return new $mol_demo_large().setup( obj => { 
			obj.name = () => this.name()
			obj.title = () => this.largeLabel()
		} )
	}

	/// childs / 
	/// 	< medium 
	/// 	< small 
	/// 	< large
	childs() {
		return [].concat( this.medium() , this.small() , this.large() )
	}

} }

