namespace $ { export class $mol_linker_demo extends $mol_rower {

	/// labelRed @ \Red
	labelRed() {
		return this.localizedText( "labelRed" )
	}

	/// linkRed $mol_linker 
	/// 	arg * color \red
	/// 	childs / < labelRed
	@ $mol_mem()
	linkRed( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => ({
			"color" : ()=> <any> "red" ,
		})
			obj.childs = () => [].concat( this.labelRed() )
		} )
	}

	/// labelGreen @ \Green
	labelGreen() {
		return this.localizedText( "labelGreen" )
	}

	/// linkGreen $mol_linker 
	/// 	arg * color \green
	/// 	childs / < labelGreen
	@ $mol_mem()
	linkGreen( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => ({
			"color" : ()=> <any> "green" ,
		})
			obj.childs = () => [].concat( this.labelGreen() )
		} )
	}

	/// labelBlue @ \Blue
	labelBlue() {
		return this.localizedText( "labelBlue" )
	}

	/// linkBlue $mol_linker 
	/// 	arg * color \blue
	/// 	childs / < labelBlue
	@ $mol_mem()
	linkBlue( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => ({
			"color" : ()=> <any> "blue" ,
		})
			obj.childs = () => [].concat( this.labelBlue() )
		} )
	}

	/// linkExternal $mol_linker 
	/// 	uri \http://example.org
	/// 	childs / \example.org
	@ $mol_mem()
	linkExternal( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.uri = () => "http://example.org"
			obj.childs = () => [].concat( "example.org" )
		} )
	}

	/// childs / 
	/// 	< linkRed 
	/// 	< linkGreen 
	/// 	< linkBlue 
	/// 	< linkExternal
	childs() {
		return [].concat( this.linkRed() , this.linkGreen() , this.linkBlue() , this.linkExternal() )
	}

} }

