namespace $ { export class $mol_floater_demo extends $mol_viewer {

	/// floaterContent $mol_carder_demo
	@ $mol_mem()
	floaterContent( next? : any ) {
		return new $mol_carder_demo()
	}

	/// floater $mol_floater childs / < floaterContent
	@ $mol_mem()
	floater( next? : any ) {
		return new $mol_floater().setup( obj => { 
			obj.childs = () => [].concat( this.floaterContent() )
		} )
	}

	/// contenter $mol_lister_demo
	@ $mol_mem()
	contenter( next? : any ) {
		return new $mol_lister_demo()
	}

	/// childs / 
	/// 	< floater 
	/// 	< contenter
	childs() {
		return [].concat( this.floater() , this.contenter() )
	}

} }

