namespace $ { export class $mol_app_demo extends $mol_stacker {

	/// items /
	items() {
		return [] as any[]
	}

	/// lister $mol_scroller childs < items
	@ $mol_mem()
	lister( next? : any ) {
		return new $mol_scroller().setup( obj => { 
			obj.childs = () => this.items()
		} )
	}

	/// main / < lister
	main() {
		return [].concat( this.lister() )
	}

	/// titleAddon @ \Demos
	titleAddon() {
		return this.localizedText( "titleAddon" )
	}

	/// options /
	options() {
		return [] as any[]
	}

	/// optioner $mol_lister rows < options
	@ $mol_mem()
	optioner( next? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.rows = () => this.options()
		} )
	}

	/// menu $mol_pager 
	/// 	title < titleAddon 
	/// 	body / < optioner
	@ $mol_mem()
	menu( next? : any ) {
		return new $mol_pager().setup( obj => { 
			obj.title = () => this.titleAddon()
			obj.body = () => [].concat( this.optioner() )
		} )
	}

	/// addon / < menu
	addon() {
		return [].concat( this.menu() )
	}

	/// welcomeText \
	welcomeText() {
		return ""
	}

	/// welcomeTexter $mol_texter text < welcomeText
	@ $mol_mem()
	welcomeTexter( next? : any ) {
		return new $mol_texter().setup( obj => { 
			obj.text = () => this.welcomeText()
		} )
	}

	/// welcomer $mol_scroller childs / < welcomeTexter
	@ $mol_mem()
	welcomer( next? : any ) {
		return new $mol_scroller().setup( obj => { 
			obj.childs = () => [].concat( this.welcomeTexter() )
		} )
	}

} }

namespace $ { export class $mol_app_demo_pager extends $mol_pager {

	/// backer_icon $mol_icon_chevron
	@ $mol_mem()
	backer_icon( next? : any ) {
		return new $mol_icon_chevron()
	}

	/// backArg * demo null
	backArg() {
		return ({
			"demo" : ()=> <any> <any> null ,
		})
	}

	/// backer $mol_linker 
	/// 	childs / < backer_icon 
	/// 	arg < backArg
	@ $mol_mem()
	backer( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.childs = () => [].concat( this.backer_icon() )
			obj.arg = () => this.backArg()
		} )
	}

	/// head / 
	/// 	< backer 
	/// 	< titler
	head() {
		return [].concat( this.backer() , this.titler() )
	}

} }

