namespace $ { export class $mol_stringer_demo extends $mol_rower {

	/// name \
	@ $mol_mem()
	name( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// one $mol_stringer value > name
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.name( next )
		} )
	}

	/// twoHint @ \Batman
	twoHint() {
		return this.localizedText( "twoHint" )
	}

	/// two $mol_stringer 
	/// 	value > name 
	/// 	hint < twoHint
	@ $mol_mem()
	two( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.name( next )
			obj.hint = () => this.twoHint()
		} )
	}

	/// threeHint @ \Jocker
	threeHint() {
		return this.localizedText( "threeHint" )
	}

	/// three $mol_stringer 
	/// 	value > name 
	/// 	hint < threeHint 
	/// 	disabled true
	@ $mol_mem()
	three( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.name( next )
			obj.hint = () => this.threeHint()
			obj.disabled = () => true
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three
	childs() {
		return [].concat( this.one() , this.two() , this.three() )
	}

} }

