namespace $ { export class $mol_labeler extends $mol_viewer {

	/// titler $mol_viewer childs / < title
	@ $mol_mem()
	titler( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.title() )
		} )
	}

	/// content null
	content() {
		return <any> null
	}

	/// contenter $mol_viewer childs / < content
	@ $mol_mem()
	contenter( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.content() )
		} )
	}

	/// childs / 
	/// 	< titler 
	/// 	< contenter
	childs() {
		return [].concat( this.titler() , this.contenter() )
	}

} }

