namespace $ { export class $mol_app_supplies_carder extends $mol_carder {

	/// supply null
	supply() {
		return <any> null
	}

	/// heightMinimal 100
	heightMinimal() {
		return 100
	}

	/// arg *
	arg() {
		return ({
		})
	}

	/// linker $mol_linker arg < arg
	@ $mol_mem()
	linker( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => this.arg()
		} )
	}

	/// childs / 
	/// 	< contenter 
	/// 	< statuser 
	/// 	< linker
	childs() {
		return [].concat( this.contenter() , this.statuser() , this.linker() )
	}

	/// codeTitle @ \Code
	codeTitle() {
		return this.localizedText( "codeTitle" )
	}

	/// code \
	code() {
		return ""
	}

	/// codeItem $mol_labeler 
	/// 	title < codeTitle 
	/// 	content < code
	@ $mol_mem()
	codeItem( next? : any ) {
		return new $mol_labeler().setup( obj => { 
			obj.title = () => this.codeTitle()
			obj.content = () => this.code()
		} )
	}

	/// costTitle @ \Cost
	costTitle() {
		return this.localizedText( "costTitle" )
	}

	/// cost $mol_unit_money valueOf 0
	@ $mol_mem()
	cost( next? : any ) {
		return new $mol_unit_money().setup( obj => { 
			obj.valueOf = () => 0
		} )
	}

	/// coster $mol_coster value < cost
	@ $mol_mem()
	coster( next? : any ) {
		return new $mol_coster().setup( obj => { 
			obj.value = () => this.cost()
		} )
	}

	/// costItem $mol_labeler 
	/// 	title < costTitle 
	/// 	content < coster
	@ $mol_mem()
	costItem( next? : any ) {
		return new $mol_labeler().setup( obj => { 
			obj.title = () => this.costTitle()
			obj.content = () => this.coster()
		} )
	}

	/// providerTitle @ \Provider
	providerTitle() {
		return this.localizedText( "providerTitle" )
	}

	/// providerName \
	providerName() {
		return ""
	}

	/// providerItem $mol_labeler 
	/// 	title < providerTitle 
	/// 	content < providerName
	@ $mol_mem()
	providerItem( next? : any ) {
		return new $mol_labeler().setup( obj => { 
			obj.title = () => this.providerTitle()
			obj.content = () => this.providerName()
		} )
	}

	/// items / 
	/// 	< codeItem 
	/// 	< costItem 
	/// 	< providerItem
	items() {
		return [].concat( this.codeItem() , this.costItem() , this.providerItem() )
	}

	/// grouper $mol_rower childs / < items
	@ $mol_mem()
	grouper( next? : any ) {
		return new $mol_rower().setup( obj => { 
			obj.childs = () => [].concat( this.items() )
		} )
	}

	/// content < grouper
	content() {
		return this.grouper()
	}

} }

