namespace $ { export class $mol_suggester extends $mol_viewer {

	/// suggests /
	suggests() {
		return [] as any[]
	}

	/// eventPress null
	@ $mol_mem()
	eventPress( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// event * keydown > eventPress
	event() {
		return $mol_merge_dict( super.event() , {
			"keydown" : ( next? : any )=> <any> this.eventPress( next ) ,
		} )
	}

	/// selectedRow 0
	@ $mol_mem()
	selectedRow( next? : any ) {
		return ( next !== void 0 ) ? next : 0
	}

	/// suggest# \
	suggest( key : any ) {
		return ""
	}

	/// eventRowerSelected# null
	@ $mol_mem_key()
	eventRowerSelected( key : any , next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// selected# false
	selected( key : any ) {
		return false
	}

	/// rower# $mol_suggester_rower 
	/// 	text < suggest# 
	/// 	prefix < value 
	/// 	eventSelected > eventRowerSelected# 
	/// 	selected < selected#
	@ $mol_mem_key()
	rower( key : any , next? : any ) {
		return new $mol_suggester_rower().setup( obj => { 
			obj.text = () => this.suggest( key )
			obj.prefix = () => this.value()
			obj.eventSelected = ( next? : any ) => this.eventRowerSelected( key , next )
			obj.selected = () => this.selected( key )
		} )
	}

	/// value \
	@ $mol_mem()
	value( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// hint \
	hint() {
		return ""
	}

	/// stringer $mol_stringer 
	/// 	value > value 
	/// 	hint < hint
	@ $mol_mem()
	stringer( next? : any ) {
		return new $mol_stringer().setup( obj => { 
			obj.value = ( next? : any ) => this.value( next )
			obj.hint = () => this.hint()
		} )
	}

	/// suggestRows /
	suggestRows() {
		return [] as any[]
	}

	/// lister $mol_lister 
	/// 	heightMinimal 0 
	/// 	childs < suggestRows
	@ $mol_mem()
	lister( next? : any ) {
		return new $mol_lister().setup( obj => { 
			obj.heightMinimal = () => 0
			obj.childs = () => this.suggestRows()
		} )
	}

	/// childs / 
	/// 	< stringer 
	/// 	< lister
	childs() {
		return [].concat( this.stringer() , this.lister() )
	}

} }

namespace $ { export class $mol_suggester_rower extends $mol_clicker {

	/// tagName \div
	tagName() {
		return "div"
	}

	/// eventSelected null
	@ $mol_mem()
	eventSelected( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// event * mousedown > eventSelected
	event() {
		return $mol_merge_dict( super.event() , {
			"mousedown" : ( next? : any )=> <any> this.eventSelected( next ) ,
		} )
	}

	/// selected false
	selected() {
		return false
	}

	/// attr * mol_suggester_selected < selected
	attr() {
		return $mol_merge_dict( super.attr() , {
			"mol_suggester_selected" : ()=> <any> this.selected() ,
		} )
	}

	/// heightMinimal 36
	heightMinimal() {
		return 36
	}

	/// text \
	text() {
		return ""
	}

	/// prefix \
	prefix() {
		return ""
	}

	/// dimmer $mol_dimmer 
	/// 	haystack < text 
	/// 	needle < prefix
	@ $mol_mem()
	dimmer( next? : any ) {
		return new $mol_dimmer().setup( obj => { 
			obj.haystack = () => this.text()
			obj.needle = () => this.prefix()
		} )
	}

	/// childs / < dimmer
	childs() {
		return [].concat( this.dimmer() )
	}

} }

