	@ $mol_mem()
	minorDisabled( next? : any ) {
		return new $mol_clicker_minor().setup( obj => { 
			obj.childs = () => [].concat( this.minorLabel() )
			obj.disabled = () => true
			obj.eventClick = ( next? : any ) => this.events( next )
		} )
	}

	/// dangerLabel @ \Be attentive!
	dangerLabel() {
		return this.localizedText( "dangerLabel" )
	}

	/// danger $mol_clicker_danger 
	/// 	childs / < dangerLabel 
	/// 	eventClick > events
	@ $mol_mem()
	danger( next? : any ) {
		return new $mol_clicker_danger().setup( obj => { 
			obj.childs = () => [].concat( this.dangerLabel() )
			obj.eventClick = ( next? : any ) => this.events( next )
		} )
	}

	/// dangerDisabled $mol_clicker_danger 
	/// 	childs / < dangerLabel 
	/// 	disabled true 
	/// 	eventClick > events
	@ $mol_mem()
	dangerDisabled( next? : any ) {
		return new $mol_clicker_danger().setup( obj => { 
			obj.childs = () => [].concat( this.dangerLabel() )
			obj.disabled = () => true
			obj.eventClick = ( next? : any ) => this.events( next )
		} )
	}

	/// childs / 
	/// 	< major 
	/// 	< majorDisabled 
	/// 	< minor 
	/// 	< minorDisabled 
	/// 	< danger 
	/// 	< dangerDisabled
	childs() {
		return [].concat( this.major() , this.majorDisabled() , this.minor() , this.minorDisabled() , this.danger() , this.dangerDisabled() )
	}

