namespace $ { export class $mol_app_inventory extends $mol_viewer {

	/// domain $mol_app_inventory_domain
	@ $mol_mem()
	domain( next? : any ) {
		return new $mol_app_inventory_domain()
	}

	/// page null
	page() {
		return <any> null
	}

	/// childs / < page
	childs() {
		return [].concat( this.page() )
	}

	/// header $mol_app_inventory_header
	@ $mol_mem()
	header( next? : any ) {
		return new $mol_app_inventory_header()
	}

	/// enter $mol_app_inventory_enter domain < domain
	@ $mol_mem()
	enter( next? : any ) {
		return new $mol_app_inventory_enter().setup( obj => { 
			obj.domain = () => this.domain()
		} )
	}

	/// controller $mol_app_inventory_controller 
	/// 	header < header 
	/// 	domain < domain
	@ $mol_mem()
	controller( next? : any ) {
		return new $mol_app_inventory_controller().setup( obj => { 
			obj.header = () => this.header()
			obj.domain = () => this.domain()
		} )
	}

	/// keeper $mol_app_inventory_keeper 
	/// 	header < header 
	/// 	domain < domain
	@ $mol_mem()
	keeper( next? : any ) {
		return new $mol_app_inventory_keeper().setup( obj => { 
			obj.header = () => this.header()
			obj.domain = () => this.domain()
		} )
	}

	/// stats $mol_app_inventory_stats 
	/// 	header < header 
	/// 	domain < domain
	@ $mol_mem()
	stats( next? : any ) {
		return new $mol_app_inventory_stats().setup( obj => { 
			obj.header = () => this.header()
			obj.domain = () => this.domain()
		} )
	}

} }

namespace $ { export class $mol_app_inventory_header extends $mol_rower {

	/// keeperLabel @ \Keeper
	keeperLabel() {
		return this.localizedText( "keeperLabel" )
	}

	/// keeperLink $mol_linker 
	/// 	arg * page \keeper
	/// 	childs / < keeperLabel
	@ $mol_mem()
	keeperLink( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => ({
			"page" : ()=> <any> "keeper" ,
		})
			obj.childs = () => [].concat( this.keeperLabel() )
		} )
	}

	/// controlLabel @ \Controller
	controlLabel() {
		return this.localizedText( "controlLabel" )
	}

	/// controlLink $mol_linker 
	/// 	arg * page \controller
	/// 	childs / < controlLabel
	@ $mol_mem()
	controlLink( next? : any ) {
		return new $mol_linker().setup( obj => { 
			obj.arg = () => ({
			"page" : ()=> <any> "controller" ,
		})
			obj.childs = () => [].concat( this.controlLabel() )
		} )
	}

	/// childs / 
	/// 	< keeperLink 
	/// 	< controlLink 
	/// 	- < statsLink
	childs() {
		return [].concat( this.keeperLink() , this.controlLink() )
	}

} }

