namespace $ { export class $mol_html_head extends $mol_viewer {

	/// tagName \head
	tagName() {
		return "head"
	}

} }

namespace $ { export class $mol_html_body extends $mol_viewer {

	/// tagName \body
	tagName() {
		return "body"
	}

} }

namespace $ { export class $mol_html_title extends $mol_viewer {

	/// tagName \title
	tagName() {
		return "title"
	}

	/// title \
	title() {
		return ""
	}

	/// childs / < title
	childs() {
		return [].concat( this.title() )
	}

} }

namespace $ { export class $mol_html_meta extends $mol_viewer {

	/// tagName \meta
	tagName() {
		return "meta"
	}

	/// name \
	name() {
		return ""
	}

	/// content \
	content() {
		return ""
	}

	/// charset \utf-8
	charset() {
		return "utf-8"
	}

	/// attr * 
	/// 	name < name 
	/// 	content < content 
	/// 	charset < charset
	attr() {
		return $mol_merge_dict( super.attr() , {
			"name" : ()=> <any> this.name() ,
			"content" : ()=> <any> this.content() ,
			"charset" : ()=> <any> this.charset() ,
		} )
	}

} }

namespace $ { export class $mol_html_link extends $mol_viewer {

	/// tagName \link
	tagName() {
		return "link"
	}

	/// rel \
	rel() {
		return ""
	}

	/// href \
	href() {
		return ""
	}

	/// attr * 
	/// 	rel < rel 
	/// 	href < href
	attr() {
		return $mol_merge_dict( super.attr() , {
			"rel" : ()=> <any> this.rel() ,
			"href" : ()=> <any> this.href() ,
		} )
	}

} }

