namespace $ { export class $mol_app_supplies_root extends $mol_stacker {

	/// entered false
	@ $mol_mem()
	entered( next? : any ) {
		return ( next !== void 0 ) ? next : false
	}

	/// enter $mol_app_supplies_enter entered > entered
	@ $mol_mem()
	enter( next? : any ) {
		return new $mol_app_supplies_enter().setup( obj => { 
			obj.entered = ( next? : any ) => this.entered( next )
		} )
	}

	/// supplies /
	supplies() {
		return [] as any[]
	}

	/// searchQuery \
	@ $mol_mem()
	searchQuery( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// lister $mol_app_supplies_lister 
	/// 	supplies < supplies 
	/// 	searchQuery > searchQuery
	@ $mol_mem()
	lister( next? : any ) {
		return new $mol_app_supplies_lister().setup( obj => { 
			obj.supplies = () => this.supplies()
			obj.searchQuery = ( next? : any ) => this.searchQuery( next )
		} )
	}

	/// supply null
	supply() {
		return <any> null
	}

	/// detailer $mol_app_supplies_detailer supply < supply
	@ $mol_mem()
	detailer( next? : any ) {
		return new $mol_app_supplies_detailer().setup( obj => { 
			obj.supply = () => this.supply()
		} )
	}

} }

