namespace $ { export class $mol_app_inventory_positioner extends $mol_rower {

	/// position null
	position() {
		return <any> null
	}

	/// title \
	title() {
		return ""
	}

	/// titler $mol_viewer childs / < title
	@ $mol_mem()
	titler( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.title() )
		} )
	}

	/// description \
	description() {
		return ""
	}

	/// descriptioner $mol_viewer childs / < description
	@ $mol_mem()
	descriptioner( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.description() )
		} )
	}

	/// producter $mol_viewer childs / 
	/// 	< titler 
	/// 	< descriptioner
	@ $mol_mem()
	producter( next? : any ) {
		return new $mol_viewer().setup( obj => { 
			obj.childs = () => [].concat( this.titler() , this.descriptioner() )
		} )
	}

	/// count 0
	@ $mol_mem()
	count( next? : any ) {
		return ( next !== void 0 ) ? next : 0
	}

	/// counter $mol_number 
	/// 	value > count 
	/// 	enabledInc false 
	/// 	enabledDec false
	@ $mol_mem()
	counter( next? : any ) {
		return new $mol_number().setup( obj => { 
			obj.value = ( next? : any ) => this.count( next )
			obj.enabledInc = () => false
			obj.enabledDec = () => false
		} )
	}

	/// status null
	@ $mol_mem()
	status( next? : any ) {
		return ( next !== void 0 ) ? next : <any> null
	}

	/// statusLabelPending @ \Pending
	statusLabelPending() {
		return this.localizedText( "statusLabelPending" )
	}

	/// statusLabelApproved @ \Approved
	statusLabelApproved() {
		return this.localizedText( "statusLabelApproved" )
	}

	/// statusLabelRejected @ \Rejected
	statusLabelRejected() {
		return this.localizedText( "statusLabelRejected" )
	}

	/// statuser $mol_switcher 
	/// 	value > status 
	/// 	options * 
	/// 		pending < statusLabelPending 
	/// 		approved < statusLabelApproved 
	/// 		rejected < statusLabelRejected
	@ $mol_mem()
	statuser( next? : any ) {
		return new $mol_switcher().setup( obj => { 
			obj.value = ( next? : any ) => this.status( next )
			obj.options = () => ({
			"pending" : ()=> <any> this.statusLabelPending() ,
			"approved" : ()=> <any> this.statusLabelApproved() ,
			"rejected" : ()=> <any> this.statusLabelRejected() ,
		})
		} )
	}

	/// childs / 
	/// 	< producter 
	/// 	< counter 
	/// 	< statuser
	childs() {
		return [].concat( this.producter() , this.counter() , this.statuser() )
	}

} }

