namespace $ { export class $mol_stacker_demo extends $mol_stacker {

	/// mainContenter $mol_pager_demo
	@ $mol_mem()
	mainContenter( next? : any ) {
		return new $mol_pager_demo()
	}

	/// main / < mainContenter
	main() {
		return [].concat( this.mainContenter() )
	}

	/// signup $mol_app_signup
	@ $mol_mem()
	signup( next? : any ) {
		return new $mol_app_signup()
	}

	/// addonContenter $mol_scroller childs / < signup
	@ $mol_mem()
	addonContenter( next? : any ) {
		return new $mol_scroller().setup( obj => { 
			obj.childs = () => [].concat( this.signup() )
		} )
	}

	/// addon / < addonContenter
	addon() {
		return [].concat( this.addonContenter() )
	}

} }

