namespace $ { export class $mol_suggester_demo extends $mol_rower {

	/// one $mol_suggester 
	/// 	focused false 
	/// 	suggests /
	@ $mol_mem()
	one( next? : any ) {
		return new $mol_suggester().setup( obj => { 
			obj.focused = () => false
			obj.suggests = () => [] as any[]
		} )
	}

	/// twoHint @ \Name
	twoHint() {
		return this.localizedText( "twoHint" )
	}

	/// suggest1 @ \Evgeny
	suggest1() {
		return this.localizedText( "suggest1" )
	}

	/// suggest2 @ \Alexander
	suggest2() {
		return this.localizedText( "suggest2" )
	}

	/// suggest3 @ \Dmitriy
	suggest3() {
		return this.localizedText( "suggest3" )
	}

	/// suggest4 @ \Yuriy
	suggest4() {
		return this.localizedText( "suggest4" )
	}

	/// suggest5 @ \Nataliya
	suggest5() {
		return this.localizedText( "suggest5" )
	}

	/// two $mol_suggester 
	/// 	hint < twoHint 
	/// 	focused true 
	/// 	suggests / 
	/// 		< suggest1 
	/// 		< suggest2 
	/// 		< suggest3 
	/// 		< suggest4 
	/// 		< suggest5
	@ $mol_mem()
	two( next? : any ) {
		return new $mol_suggester().setup( obj => { 
			obj.hint = () => this.twoHint()
			obj.focused = () => true
			obj.suggests = () => [].concat( this.suggest1() , this.suggest2() , this.suggest3() , this.suggest4() , this.suggest5() )
		} )
	}

	/// threeSuggests /
	threeSuggests() {
		return [] as any[]
	}

	/// threeCode \
	@ $mol_mem()
	threeCode( next? : any ) {
		return ( next !== void 0 ) ? next : ""
	}

	/// three $mol_suggester 
	/// 	suggests < threeSuggests 
	/// 	value > threeCode
	@ $mol_mem()
	three( next? : any ) {
		return new $mol_suggester().setup( obj => { 
			obj.suggests = () => this.threeSuggests()
			obj.value = ( next? : any ) => this.threeCode( next )
		} )
	}

	/// childs / 
	/// 	< one 
	/// 	< two 
	/// 	< three
	childs() {
		return [].concat( this.one() , this.two() , this.three() )
	}

} }

